import { Component, OnInit, ViewChild, AfterViewInit, Output, SimpleChanges } from '@angular/core';

import { ActivatedRoute } from '@angular/router';

import { EventEmitter } from 'events';
import { OnChanges } from '@angular/core/src/metadata/lifecycle_hooks';
import { YoutubeAutoCompleteComponent } from '../../components/core/youtube-auto-complete/youtube-auto-complete.component';
import { SharingService, Data } from '../../services/sharing.service';

@Component({
  selector: 'oorga-search-result',
  templateUrl: './search-result.component.html',
  styleUrls: ['./search-result.component.css']
})
export class SearchResultComponent implements OnInit{

  thumbnailData: any[] = [];

  constructor(private share: SharingService) {
  }

  ngOnInit() {
    this.share.getObservable().subscribe(result => {
      if (Data.VIDEO_RESULT === result.getKey()) {
        this.thumbnailData = result.getData();
      }
    });
  }


}



