import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from '../app.component';
import { HomepageComponent } from '../pages/homepage/homepage.component';
import { YoutubePlayerPageComponent } from '../pages/youtube-player-page/youtube-player-page.component';
import { SearchResultComponent } from '../pages/search-result/search-result.component';
import { BasePageComponent } from '../pages/base-page/base-page.component';
import { SyncPlayerComponent } from '../pages/sync-player/sync-player.component';


const routes: Routes = [
  {
    path: '', component: HomepageComponent
  },
  {
    path: 'view/play/:videoId', component: YoutubePlayerPageComponent
  },
  {
    path: 'view/search/:searchQuery', component: SearchResultComponent
  },
  {
    path: 'view/playSync/:videoId/:groupId', component: SyncPlayerComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    enableTracing: true
  })],
  exports: [RouterModule]
})
export class RoutingModule { }



