import { Component, OnInit } from '@angular/core';
import { OorgaFacebookService } from '../../services/oorga-facebook.service';

@Component({
  selector: 'oorga-fb-login-button',
  templateUrl: './fb-login-button.component.html',
  styleUrls: ['./fb-login-button.component.css']
})
export class FbLoginButtonComponent implements OnInit {

  constructor(private fb: OorgaFacebookService) {

  }


  display: string;

  status: string;

  performAction() {
    if (this.status == "connected") {
      this.fb.logOut().subscribe(res => this.updateButtonText(res));
    } else {
      this.fb.login().subscribe(res => this.updateButtonText(res));;
    }
  }


  ngOnInit() {
    this.fb.getStatus().subscribe(res => {
      this.updateButtonText(res);
    });
  }

  updateButtonText(res) {
    this.status = res;
    if (res == "connected") {
      this.display = "Logout";
    } else {
      this.display = "Login with Facebook";
    }
  }
}



