import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { YoutubeApiService } from '../../../services/youtube-api.service';
import { Data } from '../../../services/sharing.service';
import { OnChanges } from '@angular/core/src/metadata/lifecycle_hooks';

@Component({
  selector: 'oorga-detailed-player',
  templateUrl: './detailed-player.component.html',
  styleUrls: ['./detailed-player.component.css']
})
export class DetailedPlayerComponent implements OnChanges {

  constructor(private api: YoutubeApiService) { }

  title: string;
  like: number;
  dislike: number;
  view: number;
  description: string;

  @Input() videoId: string;

  @Input() showJoin: boolean = true;

  @Output() change = new EventEmitter<any>();

  @Output() ready = new EventEmitter<any>();

  @Output() join = new EventEmitter<any>();

  onReady(event) {
    this.ready.emit(event);
  }

  onChange(event) {
    this.change.emit(event);
  }

  onJoin(event) {
    this.join.emit(event);
  }

  @Input() data: any;

  ngOnChanges() {

    if (this.data) {

      this.data = this.data['items'][0];
      if (this.data) {
        this.title = this.data.snippet.title;
        this.view = this.data.statistics.viewCount;
        this.like = this.data.statistics.likeCount;
        this.dislike = this.data.statistics.dislikeCount;
        this.description = this.data.snippet.description;
      }

    }
  }

}



