import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';
import { Duration } from 'moment';

@Pipe({
  name: 'durationFormatter'
})
export class DurationFormatterPipe implements PipeTransform {

  transform(value: any, args?: any): string {
    return this.getDisplayTime(value);
  }

  getDisplayTime(duation: string) {
    let du = moment.duration(duation);
    let milli = du.asMilliseconds();
    var milliseconds = milli % 1000;
    var seconds = Math.floor((milli / 1000) % 60);
    var minutes = Math.floor((milli / (60 * 1000)) % 60);

    return this.msToTime(milli);
  }

  msToTime(s) {
    var ms = s % 1000;
    s = (s - ms) / 1000;
    var secs = s % 60;
    s = (s - secs) / 60;
    var mins = s % 60;
    var hrs = (s - mins) / 60;

    return (hrs ? this.pad(hrs) + ':' : "") +  this.pad(mins)+":" + this.pad(secs);
  }
  
  pad(n, z?) {
    z = z || 2;
    return ('00' + n).slice(-z);
  }

}



