import { Component, OnInit, ViewEncapsulation, Input } from '@angular/core';

@Component({
  selector: 'media-thumbnail-list',
  templateUrl: './media-thumbnail-list.component.html',
  styleUrls: ['./media-thumbnail-list.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class MediaThumbnailListComponent implements OnInit {

  @Input() datas: any[];

  constructor() {

  }

  @Input() navigationParams = [];

  @Input() urlToNavigate:string;

  getURL(videoId) {
    let url=[this.urlToNavigate,videoId];
    return this.navigationParams? url.concat(this.navigationParams):url;
  }
  
  ngOnInit() {
  }

}



