import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

 


export class Data {

  public static VIDEO_RESULT="VIDEO_RESULT";
  
  public static SUGGESTION_RESULT="SUGGESTION_RESULT";

  public static PARAM_UPDATED="PARAM_UPDATED";

  public static RELATED_VIDEO_RESULT="RELATED_VIDEO_RESULT";

  public static VIDEO_DETAIL="VIDEO_DETAIL";

  public static apiKey="AIzaSyAkc_Bd_mpyn-MdyI91dp87AQqIRyZQVNM";

  public static TOGGLE="TOGGLE";

  public static CONNECT="CONNECT";

  key: string;
  data: any;

  constructor(key: string, data: any) {
    this.data = data;
    this.key = key;
  }
  getKey(): string {
    return this.key;
  }
  setKey(key: string) {
    this.key = key;
  }
  setData(data: any) {
    this.data = data;
  }
  getData(): any {
    return this.data;
  }

}
@Injectable()
export class SharingService {

  private broadCaster = new Subject<Data>();

  private broadCasterObservable = this.broadCaster.asObservable();

  constructor() { }

  public publishData(key: string, data: any) {
    let dataToShare = new Data(key, data);
    this.broadCaster.next(dataToShare);
  }

  public getObservable(): Observable<Data> {
    return this.broadCasterObservable;
  }
}



