import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FacebookModule, FacebookService } from 'ngx-facebook';
import { OorgaFacebookService } from './services/oorga-facebook.service';
import { FbLoginButtonComponent } from './components/fb-login-button/fb-login-button.component';

@NgModule({
  imports: [
    CommonModule,FacebookModule.forRoot()
  ],
  exports:[FbLoginButtonComponent],
  declarations: [FbLoginButtonComponent],
  providers:[FacebookService,OorgaFacebookService]
})
export class OorgaFacebookModule { }



