import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class YoutubeApiService {

  searchUrl: string = '/api/googleapis/youtube/v3/search';

  searchMutlipleVideos = '/api/googleapis/youtube/v3/videos';

  suggestionUrl: string = '/api/suggestgoogle/complete/search';

  constructor(private http: HttpClient) { }

  public searchYoutube(query: string, apiKey: string) {
    if (query) {
      let param = new HttpParams();
      param = param.set('maxResults', '25');
      param = param.set('part', 'id');
      param = param.set('type', 'video');
      param = param.set('q', query);
      param = param.set('key', apiKey);

      return this.handleGetRequests(this.searchUrl, param).map(
        res => {
          let videoIds = res['items'].map(e => e.id.videoId);
          return videoIds;
        }
      );
    }
  }

  fetchSearchResults(searchQuery: string, apiKey: string) {
    return this.searchYoutube(searchQuery, apiKey).flatMap(data => {
      return this.searchWithVideoIds(data, apiKey);
    });
  }

  public searchWithVideoIds(videoIds: string[], apiKey: string) {
    if (videoIds) {
      let param = new HttpParams();
      param = param.set('part', 'id,snippet,contentDetails,statistics');
      param = param.set('key', apiKey);
      param = param.set('id', videoIds.join(','));

      return this.handleGetRequests(this.searchMutlipleVideos, param);
    }
  }


  fetchVideoDetails(videoId: string, apiKey: string) {
    if (videoId) {
      return this.searchWithVideoIds([videoId], apiKey);
    }
  }


  fetchSuggestions(searchQuery) {
    if (searchQuery) {
      let param = new HttpParams();
      param = param.set('hl', 'en');
      param = param.set('ds', 'yt');
      param = param.set('client', 'firefox');
      param = param.set('hjson', 't');
      param = param.set('cp', '1');
      param = param.set('q', searchQuery);
      param = param.set('format', '5');
      param = param.set('alt', 'json');

      return this.handleGetRequests(this.suggestionUrl, param);
    }
  }

  fetchTrendingVideos(apiKey: string, countryCode?: string, category?: string) {
    let param = new HttpParams();
    param = param.set('part', 'id,snippet,contentDetails,statistics');
    param = param.set('chart', 'mostPopular');
    param = param.set('regionCode', countryCode ? countryCode : 'IN');
    param = param.set('videoCategoryId', category ? category : '');
    param = param.set('key', apiKey);
    param = param.set('maxResults', '28');

    return this.handleGetRequests(this.searchMutlipleVideos, param);
  }


  handleGetRequests(url: string, params: HttpParams): Observable<Object> {
    return this.http.get(url, {
      params: params
    });
  }

  fetchRelatedVideos(videoId: string, apiKey: string) {
    let param = new HttpParams();
    param = param.set("part", "id");
    param = param.set("relatedToVideoId", videoId);
    param = param.set("type", "video");
    param = param.set('key', apiKey);
    return this.handleGetRequests(this.searchUrl, param).map(
      res => {
        let videoIds = res['items'].map(e => e.id.videoId);
        return videoIds;
      }
    );
  }

  fetchRelatedVideosWithDetails(videoId: string, apiKey: string) {
    return this.fetchRelatedVideos(videoId, apiKey).flatMap(data => {
      return this.searchWithVideoIds(data, apiKey);
    });
  }
}



