import { Injectable } from '@angular/core';
import { FacebookService, InitParams, LoginResponse } from 'ngx-facebook';
import { LoginStatus } from 'ngx-facebook/dist/esm/models/login-status';
import { OnInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { UIResponse } from 'ngx-facebook/dist/esm/models/ui-response';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class OorgaFacebookService implements OnInit {

  ngOnInit(): void {


  }

  getStatus() {
    return Observable.fromPromise(this.fb.getLoginStatus()).map(res => {
      if (res.authResponse) {
        this.isLoggedIn = res.status;
        this.authResponse = res.authResponse;
        this.userName = res.authResponse.userID;
      }
      return this.isLoggedIn;
    });
  }

  constructor(private fb: FacebookService) {
    let initParams: InitParams = {
      appId: '1956226621059583',
      xfbml: true,
      version: 'v2.11'
    };

    fb.init(initParams);
  }

  invite(url: string): Promise<UIResponse> {
    url = url.replace('localhost', '10.0.0.4:4000')
    return this.fb.ui({
      method: 'send',
      message: 'Watch with me',
      link: url,
      display: 'dialog'
    });

  }

  isLoggedIn;
  authResponse;
  userName: string;

  getUserName(id: string) {
    let params= { fields: 'name,picture' };
    if (this.isLoggedIn) {
      return Observable.fromPromise(this.fb.api('/' + id, 'get',params));
    } else {
      return this.getStatus().map(() => {
        return Observable.fromPromise(this.fb.api('/' + id, 'get', params));
      });
    }
  }


  login(): Observable<LoginResponse> {
    return Observable.fromPromise(this.fb.login())
      .map((response: LoginResponse) => {
        this.isLoggedIn = response.status;
        this.authResponse = response.authResponse;
        this.userName = response.authResponse.userID;
        console.log('Logged in', response);
        return this.isLoggedIn;
      });

  }

  logOut() {
    return Observable.fromPromise(this.fb.logout()).map(res => {
      this.isLoggedIn = "disconnected";
      return this.isLoggedIn;
    });
  }


}



