import { Injectable } from '@angular/core';
import { StompService } from 'ng2-stomp-service';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/observable/fromPromise';

import { Observable } from 'rxjs/Observable';
import { ReplaySubject } from 'rxjs/ReplaySubject';
import { Subject } from 'rxjs/Subject';
@Injectable()
export class SyncWsService {


  public static confUrl: string = '/api/ws/websocket';

  constructor(public stomp: StompService) {
    this.configure();
  }


  map: Map<string, Subscription> = new Map<string, Subscription>();

  connected: boolean = false;

  connectionPromise: Promise<{}>;


  connection: ReplaySubject<any> = new ReplaySubject(1);
  get() {
    if (this.stomp.status != "CONNECTED" && this.stomp.status != "CONNECTING") {
      Observable.fromPromise(this.stomp.startConnect()).subscribe(() => this.connection.next(this.stomp.status));
    }
    return this.connection;
  }

  configure(): void {
    this.stomp.configure({
      host: SyncWsService.confUrl,
      debug: true,
      queue: { 'init': false }
    });
  }

  subscribe(url: string): Observable<any> {
    let broadCaster = new Subject<any>();
    this.get().subscribe(() => {
      let existingSubscription = this.map.get(url);
      if (existingSubscription) {
        existingSubscription.unsubscribe();
      }
      let subscription = this.stomp.subscribe(url, (res) => {
        broadCaster.next(res);
      });
      this.map.set(url, subscription);
    });

    return broadCaster.asObservable();
  }

  unsubscribe(url: string) {
    this.get().subscribe(() => {
      let subscription = this.map.get(url);
      if (subscription) {
        subscription.unsubscribe();
      }
    });
  }

  send(url: string, data: any) {
    this.get().subscribe(() => {
      this.stomp.send(url, data);
    });
  }

  disconnect() {
    this.stomp.disconnect().then(() => {
      console.log('Connection closed')
    });
  }
}



