import { Injectable } from '@angular/core';
import { WsDataService } from './ws-data.service';
import { SyncWsService } from './sync-ws.service';
import { SyncData, PlayerData } from './syncData';
import { YoutubePlayerService } from '../../../services/youtube-player.service';

@Injectable()
export class WsPlayerService {

  constructor(private wsService: SyncWsService, private dataService: WsDataService, private playerApi: YoutubePlayerService) { }

  player: YT.Player;

  updateServer(url: string, data: SyncData) {
    this.wsService.send(url, data);
  }

  updateServerForJoining(player: YT.Player, groupId?: string) {
    if (groupId) { this.dataService.setGroupId(groupId); }
    this.player = player;
    this.wsService.subscribe("/receive/getHelp-" + this.dataService.getGroupId()).subscribe((data) => {
      this.getHelp(data);
    });
    this.wsService.subscribe("/receive/helpOthers-" + this.dataService.getGroupId()).subscribe(() => {
      this.helpOthers();
    });
    this.updateServer('/send/joined', this.dataService.prepareData(player));
  }

  getHelp(data: SyncData) {
    if (data.playerData && data.userName != this.dataService.getUserName()) {
      this.checkAndInit(data.playerData.videoId, this.player);
      this.callback(data.playerData);
    }
    this.dataService.setProgramAction(false);
    this.wsService.unsubscribe("/receive/getHelp-" + this.dataService.getGroupId());
    this.wsService.subscribe("/receive/get-" + this.dataService.getGroupId()).subscribe((data) => {
      this.updatePlayerOnResponse(data);
    });
  }

  helpOthers() {
    this.updateServer('/send/offerHelp', this.dataService.prepareData(this.player));
  }


  updatePlayerOnResponse(data: SyncData) {
    if (data.playerData && data.userName != this.dataService.getUserName() && !data.programAction) {
      this.checkAndInit(data.playerData.videoId, this.player);
      this.callback(data.playerData);
    }
  }

  checkAndInit(responseVideoId: string, player: YT.Player) {
    if (responseVideoId != this.dataService.getVideoId(player)) {
      this.playerApi.playVideo(responseVideoId, player);
    }
  }

  callback(response: PlayerData) {
    var seekAhead = true;
    switch (response.playerState) {
      case YT.PlayerState.PLAYING:
        this.dataService.setProgramAction(true);
        this.seek(response, seekAhead);
        this.player.playVideo();
        break;
      case YT.PlayerState.PAUSED:
        this.dataService.setProgramAction(true);
        this.player.pauseVideo();
        this.seek(response, seekAhead);
        break;
      case YT.PlayerState.BUFFERING:
        // seek(response, seekAhead, player);
        break;
      case YT.PlayerState.ENDED:
        console.log("ended");
        break;
    }
  }

  seek(response: PlayerData, seekAhead) {
    this.player.seekTo(response.duration, seekAhead);
  }

  public decideAndUpdateServer(player: YT.Player, event: YT.OnStateChangeEvent): boolean {
    let isServerUpdated = false;
    if (!this.dataService.getProgramAction()) {
      console.log("decided to update the server");
      this.updateServer('/send/update', this.dataService.prepareData(player));
      isServerUpdated = true;
    }
    if (event.data != YT.PlayerState.BUFFERING) {
      this.dataService.setProgramAction(false);
    }
    return isServerUpdated;
  }

  generateGroupId(userName: string): string {
    return this.dataService.getGroupId("new", userName)
  }

}



