import { Component, SimpleChanges,ViewChild} from '@angular/core';
import { SharingService, Data } from './services/sharing.service';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { UrlParameterProviderService } from './services/url-parameter-loader.service';
import { OnChanges, OnInit } from '@angular/core/src/metadata/lifecycle_hooks';
import { ObservableMedia } from '@angular/flex-layout';
import { Observable } from 'rxjs/Observable';

import { MatSidenav } from '@angular/material';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  public cols: Observable<number>;

  @ViewChild(MatSidenav)
  sideNav: MatSidenav;

  ngOnInit() {
    const grid = new Map([
      ["xs", 1],
      ["sm", 2],
      ["md", 3],
      ["lg", 4],
      ["xl", 4]
    ]);
    let start: number;
    grid.forEach((cols, mqAlias) => {
      if (this.observableMedia.isActive(mqAlias)) {
        start = cols;
      }
    });
    this.cols = this.observableMedia.asObservable()
      .map(change => {
        console.log(change);
        console.log(grid.get(change.mqAlias));
        return grid.get(change.mqAlias);
      })
      .startWith(start);

    this.share.getObservable().subscribe(res => {
      if (Data.TOGGLE == res.getKey()) {
        this.sideNav.toggle();
      }
    });
  }

  searchQuery: string;

  constructor(private share: SharingService, private router: Router, private observableMedia: ObservableMedia) {

  }


  isLargeScreen() {
    const width = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
    if (width > 720) {
      return true;
    } else {
      return false;
    }
  }


}



