import { Directive,ElementRef,Output, HostListener,Input,EventEmitter } from '@angular/core';


@Directive({
  selector: '[HideOnClickingOther]'
})
export class HideOnClickingOtherDirective {

  @Input() watchElement: string;

  @Output() public clickOutside = new EventEmitter();

  @HostListener('document:click', ['$event.target'])
  public onClick(targetElement) {
    if (targetElement.id == this.watchElement) {
      return;

    }
    if (!this.elementRef.nativeElement.contains(targetElement)) {
      this.clickOutside.emit(null);
    }
  }
  constructor(private elementRef: ElementRef) { }

}



