import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {
  MatAutocompleteModule,
  MatButtonModule,
  MatIconModule,
  MatInputModule,
  MatSidenavModule,
  MatTooltipModule,
  MatCardModule,
  MatGridListModule
} from '@angular/material';

@NgModule({
  imports: [BrowserAnimationsModule, MatButtonModule, MatAutocompleteModule],
  exports: [BrowserAnimationsModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatIconModule,
    MatInputModule, 
    MatSidenavModule,
    MatTooltipModule,
    MatCardModule,
    MatGridListModule],
  declarations: []
})
export class AngularMaterialModule { }



