import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SharingService, Data } from '../../services/sharing.service';
import 'rxjs/add/operator/filter';

@Component({
  selector: 'oorga-base-page',
  templateUrl: './base-page.component.html',
  styleUrls: ['./base-page.component.css']
})
export class BasePageComponent implements OnInit {

  searchQuery:string;

  constructor(private share: SharingService, private route: ActivatedRoute) {
      // this.route.firstChild.params.subscribe(params => {
      //   this.searchQuery = params['searchQuery'];
      // });
   }

  ngOnInit() {
    this.share.getObservable().filter(res=>res.getKey()==Data.PARAM_UPDATED).subscribe(res=>{
      this.searchQuery=res.getData();
    });
  }

  updateSearchParam(event){
    this.searchQuery=event;
  }
  updateThumbnails(data) {
    this.share.publishData(Data.VIDEO_RESULT, data['items']);
  }
}



