import { Injectable } from '@angular/core';
import { SyncData } from './syncData';

@Injectable()
export class WsDataService {

  constructor() { }

  groupId: string;

  getGroupId(type?: string,userName?:string): string {
    if (type == "new") {
      this.userName=userName;
      this.groupId = userName+this.randomString(10);
    }
    return this.groupId
  }

  setGroupId(groupId:string){
    this.groupId=groupId;
  }

  userName: string = Date.now().toString();

  programAction: boolean = false;

  getProgramAction(): boolean {
    return this.programAction;
  }

  setProgramAction(programAction: boolean) {
    this.programAction = programAction;
  }

  getUserName(): string {
    return this.userName;
  }

  getVideoId(player) {
    return player.getVideoData()['video_id'];
  }

  prepareData(player: YT.Player, programAction?: boolean, isHelp?: boolean): SyncData {
    return {
      accessLevel: 1,
      actionSource: "",
      userName: this.getUserName(),
      programAction: this.getProgramAction(),
      groupId: this.getGroupId("old"),
      isHelp: isHelp,

      playerData: {
        playerState: player.getPlayerState(),
        duration: player.getCurrentTime(),
        videoId: this.getVideoId(player)
      }
    };
  }

  randomString(length: number): string {
    let text = "";
    let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (let i = 0; i < length; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text + Date.now().toString();
  }
}



