import { Component, OnInit,Output, EventEmitter } from '@angular/core';
import { SharingService, Data } from '../../../services/sharing.service';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'oorga-search-header',
  templateUrl: './search-header.component.html',
  styleUrls: ['./search-header.component.css']
})
export class SearchHeaderComponent implements OnInit {


  searchQuery;



  constructor(private share: SharingService, private route: ActivatedRoute) {
  }


  ngOnInit() {
    this.route.params.subscribe(params => {
      this.searchQuery = params['searchQuery'];
    });
  }
  updateThumbnails(data) {
    this.share.publishData(Data.VIDEO_RESULT, data['items']);
  }

  pushEvent(){
    this.share.publishData(Data.TOGGLE, "toggle");
  }
}



