import { Injectable } from '@angular/core';
import { SyncWsService } from '../../sync/services/sync-ws.service';
import { StompService } from 'ng2-stomp-service';
import { Message, MessageType } from '../components/message/message.component';
import { OorgaFacebookService } from '../../facebook/services/oorga-facebook.service';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class ChatService {

  groupId: string;

  constructor(public ws: SyncWsService, private fb: OorgaFacebookService) {

  }

  messageBroadcaster: Subject<Message> = new Subject();

  getMessageUpdates() {
    return this.messageBroadcaster;
  }
  subscribeToMessages(groupId: string) {
    this.groupId = groupId;
    return this.ws.subscribe('/receive/chat/getmessages/' + groupId).subscribe((res) => {
      this.fb.getUserName(res.userName).subscribe((profile) => {
        res.userName = profile.name;
        res.profilePic=profile.picture.data.url;
        this.messageBroadcaster.next(res);
      });
    });
  }

  sendMessageService(msg: Message) {
    this.ws.send('/send/chat/sendmessage', msg);
  }

  convertPlayerStatusToMessage(userName: string, status: string): string {
    return (status == YT.PlayerState.PAUSED.toString() ? "Paused the video" : "Played the video");
  }

  sendMessage(message: string, type: MessageType) {
    if (type == 'SYS') {
      if (message != YT.PlayerState.PAUSED.toString() && message != YT.PlayerState.PLAYING.toString()) {
        return;
      }
      message = this.convertPlayerStatusToMessage(this.fb.userName, message);
    }
    this.sendMessageService(new Message(this.fb.userName, Date.now(), message, this.groupId, type));
  }
}



