import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { OnChanges } from '@angular/core/src/metadata/lifecycle_hooks';

export declare type MessageType = 'SYS' | 'USR';

export class Message {
  messagetype: string;
  userName: string;
  content: string;
  time: number;
  groupId: string;
  type: string;

  constructor(userName: string, time: number, content: string, groupId: string, type: string, profilePic?: string) {
    this.content = content;
    this.messagetype = profilePic;
    this.time = time;
    this.userName = userName;
    this.groupId = groupId;
    this.type = type;
  }
}

@Component({
  selector: 'message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnChanges {


  @Input() msg: Message;

  user: boolean;

  constructor() { }

  ngOnInit() {
    if (this.msg && this.msg.type) {
      this.user = this.msg.type.toString() == "USR" ? true : false
    };
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (this.msg && this.msg.type) {
      this.user = this.msg.type.toString() == "USR" ? true : false;
    }
  }
}



