import { Injectable } from '@angular/core';
import { WsPlayerService } from '../modules/sync/services/ws-player.service';
import { OorgaFacebookService } from '../modules/facebook/services/oorga-facebook.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class ConnectService {

  constructor(private syncService: WsPlayerService, private fb: OorgaFacebookService, private router: Router) { }

  inviteOthers(url: string, videoId: string) {
    this.checkFBLoginStatus().subscribe(res => {
      console.log("join freinds:" + res);
      console.log(res);
      if (res) {
        this.invite(url, videoId, this.fb.userName).subscribe(res => {
          if (res) { this.router.navigate([res.url]); }
        });
      }

    });

  }

  checkFBLoginStatus(): Observable<boolean> {
    let result = new Subject<boolean>();
    this.fb.getStatus().subscribe(res => {
      console.log("join freinds:");
      console.log(res);
      if (res) {
        result.next(true);
      }
      else {
        return this.fb.login().subscribe(res => {
          result.next(true);
        });
      }
    });
    return result.asObservable();
  }
  private invite(toUrl: string, videoId: string, userId: string): Observable<any> {
    let url = "/" + toUrl + "/" + videoId + "/" + this.syncService.generateGroupId(userId);
    let host = window.location.hostname;
    console.log("URL=" + url);
    let resp = Observable.fromPromise(this.fb.invite(host + url));
    return resp.map((res) => {
      return {
        response: res,
        url: url,
        host: host
      };
    });
  }
}



