import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { YoutubeApiService } from '../../services/youtube-api.service';
import { SharingService, Data } from '../../services/sharing.service';
import { WsPlayerService } from '../../modules/sync/services/ws-player.service';
import { YoutubePlayerService } from '../../services/youtube-player.service';
import { OorgaFacebookService } from '../../modules/facebook/services/oorga-facebook.service';
import { ChatService } from '../../modules/chat/services/chat-service.service';

@Component({
  selector: 'oorga-sync-player',
  templateUrl: './sync-player.component.html',
  styleUrls: ['./sync-player.component.css']
})
export class SyncPlayerComponent implements OnInit {

  constructor(private route: ActivatedRoute, private ytApi: YoutubeApiService, private share: SharingService,
    private playerApi: YoutubePlayerService, private syncService: WsPlayerService, private fb: OorgaFacebookService, private chat: ChatService) { }

  sub;

  player: YT.Player;
  private videoId: string;

  groupId: string;

  datas: any[];

  videoDetail;

  savePlayer(player) {
    this.player = player;
    // player.playVideo();
    console.log('player instance', player);
    this.enableConnect = true;
    this.updateServerThatConnected(this.groupId);
    this.chat.subscribeToMessages(this.groupId);
  }

  enableConnect: boolean = false;

  onStateChange(event: YT.OnStateChangeEvent) {
    console.log('player state', event.data);
    if (this.enableConnect) {
      console.log("connect enabled");
      if (this.syncService.decideAndUpdateServer(this.player, event)) {
        this.chat.sendMessage(event.data.toString(),"SYS");
      }
    }
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.videoId = params['videoId'];
      this.groupId = params['groupId'];
      if (this.player) { this.playerApi.playVideo(this.videoId, this.player); }


      this.ytApi.fetchRelatedVideosWithDetails(this.videoId, Data.apiKey).subscribe(res => {
        this.share.publishData(Data.RELATED_VIDEO_RESULT, res["items"]);
      });

      this.ytApi.fetchVideoDetails(this.videoId, Data.apiKey).subscribe(res => {
        this.videoDetail = res;
      });
    });
    this.share.getObservable().subscribe(res => {
      if (Data.RELATED_VIDEO_RESULT === res.getKey()) {
        this.datas = res.getData();
        console.log(this.datas);
      }
    });

  }


  updateServerThatConnected(groupId: string) {
    this.syncService.updateServerForJoining(this.player, groupId);
  }
}



