import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { YtPlayerComponent } from './components/core/yt-player/yt-player.component';
import { YoutubePlayerService } from './services/youtube-player.service';
import { MediaThumbnailComponent } from './components/core/media-thumbnail/media-thumbnail.component';
import { MediaThumbnailListComponent } from './components/core/media-thumbnail-list/media-thumbnail-list.component';
import { YoutubeAutoCompleteComponent } from './components/core/youtube-auto-complete/youtube-auto-complete.component';
import { HideOnClickingOtherDirective } from './directives/hide-on-clicking-other.directive';
import { FormsModule, ReactiveFormsModule,FormBuilder } from '@angular/forms';
import { SearchFilterPipe } from './pipes/search-filter.pipe';
import {HttpClientModule, HttpParams} from '@angular/common/http';
import { YoutubeApiService } from './services/youtube-api.service';
import { YoutubePlayerPageComponent } from './pages/youtube-player-page/youtube-player-page.component';
import { ActivatedRoute, RouterModule } from '@angular/router';
import { RoutingModule } from './routing/routing.module';
import { HomepageComponent } from './pages/homepage/homepage.component';
import { SharingService } from './services/sharing.service';
import { SearchResultComponent } from './pages/search-result/search-result.component';
import { MediaThumbnailGridComponent } from './components/core/media-thumbnail-grid/media-thumbnail-grid.component';
import { MediaDetailComponent } from './components/core/media-detail/media-detail.component';
import { SideBarComponent } from './components/core/side-bar/side-bar.component';
import { UrlParameterProviderService } from './services/url-parameter-loader.service';
import { BasePageComponent } from './pages/base-page/base-page.component';
import { ViewCountFormatPipe } from './pipes/view-count-formatter.pipe';
import { PadLengthPipe } from './pipes/pad-length.pipe';
import { AngularMaterialModule } from './modules/angular-material/angular-material.module';
import { FlexLayoutModule } from "@angular/flex-layout";
import { DurationFormatterPipe } from './pipes/duration-formatter.pipe';
import { SearchHeaderComponent } from './components/enhanced/search-header/search-header.component';
import { SyncModule } from './modules/sync/sync.module';
import { StompService } from 'ng2-stomp-service/dist/stomp.service';
import { SyncPlayerComponent } from './pages/sync-player/sync-player.component';
import { OorgaFacebookModule } from './modules/facebook/Oorga-facebook.module';
import { ConnectService } from './services/connect.service';
import { DetailedPlayerComponent } from './components/enhanced/detailed-player/detailed-player.component';
import { ChatModule } from './modules/chat/chat.module';




@NgModule({
  declarations: [
    AppComponent,
    YtPlayerComponent,
    MediaThumbnailComponent,
    MediaThumbnailListComponent,
    YoutubeAutoCompleteComponent,
    HideOnClickingOtherDirective,
    SearchFilterPipe,
    YoutubePlayerPageComponent,
    HomepageComponent,
    SearchResultComponent,
    MediaThumbnailGridComponent,
    MediaDetailComponent,
    SideBarComponent,
    BasePageComponent,
    ViewCountFormatPipe,
    PadLengthPipe,
    DurationFormatterPipe,
    SearchHeaderComponent,
    SyncPlayerComponent,
    DetailedPlayerComponent
  ],
  imports: [
    BrowserModule,ReactiveFormsModule,FormsModule,HttpClientModule,RoutingModule, RouterModule,AngularMaterialModule,FlexLayoutModule,SyncModule,OorgaFacebookModule,ChatModule
  ],
  providers: [
    YoutubePlayerService,FormBuilder,YoutubeApiService,SharingService,StompService,ConnectService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



