import { Component, OnInit, EventEmitter, Output, Input, SimpleChanges,OnChanges } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import 'rxjs/add/operator/mergeMap';
import { YoutubeApiService } from '../../../services/youtube-api.service';
import { Data } from '../../../services/sharing.service';


@Component({
  selector: 'youtube-auto-complete',
  templateUrl: './youtube-auto-complete.component.html',
  styleUrls: ['./youtube-auto-complete.component.css']
})
export class YoutubeAutoCompleteComponent implements OnInit,OnChanges {
 
  ngOnChanges(changes: SimpleChanges): void {
      this.fetchSearchResults();
  }

  @Output() searchResult = new EventEmitter();

  @Output() menuClicked = new EventEmitter();

  suggestCallBack: any = [];

  apiKey = Data.apiKey;

  results: any = [];

  @Input() searchQuery: string;

  test;

  showDropDown: boolean = false;

  quantity;

  pushEvent(){
    this.menuClicked.emit("toggle");
  }

  constructor(private http: HttpClient, private ytApi: YoutubeApiService) {

  }

  toggleDropDown() {
    this.showDropDown = (!this.showDropDown && this.results.length);
  }

  ngOnInit() {
    
  } 

  getSearchValue() {
    return this.searchQuery;
  }

  setSelectedValue(searchQuery) {
    this.searchQuery = searchQuery;
    this.toggleDropDown();
  }

  fetchSuggestions(event) {
    if (event) {
      this.ytApi.fetchSuggestions(this.getSearchValue()).subscribe(data => {
        this.results = data[1];
        this.showDropDown = (this.results.length);
      });
    }
  }

  fetchSearchResults() {
    if (this.getSearchValue()) {
      this.ytApi.fetchSearchResults(this.getSearchValue(), this.apiKey).subscribe(data => {
        this.searchResult.emit(data);
        console.log("data from seach" + data);
      });
    }
  }
}




