import { Component, OnInit } from '@angular/core';
import { Message } from '../message/message.component';
import { ChatService } from '../../services/chat-service.service';
import { WsDataService } from '../../../sync/services/ws-data.service';

declare var $: any;


@Component({
  selector: 'oorga-chat-window',
  templateUrl: './chat-window.component.html',
  styleUrls: ['./chat-window.component.css']
})
export class ChatWindowComponent implements OnInit {

  messages: Message[] = [];

  typedMessage: string;

  unreadMessages: number=0;

  showCounter:boolean=false;

  constructor(private chatSer: ChatService, private dataService: WsDataService) { }

  ngOnInit() {
    this.getMessages(this.dataService.getGroupId());
  }

  getMessages(groupId: string) {
    this.chatSer.getMessageUpdates().subscribe((res) => {
      if ($(".chat").is(":hidden")) { this.showCounter=true; this.unreadMessages++; }
      this.messages.push(res);
      var elem = document.getElementById('chat-history');
      setTimeout(()=>elem.scrollTop = elem.scrollHeight,100);
    });
  }

  sendMessage(event) {
    let msg = this.typedMessage;
    this.chatSer.sendMessage(msg, "USR");
    this.typedMessage = "";
  }

  minimize() {
    $('.chat').slideToggle(300, 'swing');
    $('.chat-message-counter').fadeToggle(300, 'swing');
    if(this.unreadMessages)
    {
      this.unreadMessages=0;
      this.showCounter=false;
    }
  }

  close(e) {
    e.preventDefault();
    $('#live-chat').fadeOut(300);
  }
}




