import { Component, OnInit } from '@angular/core';
import { YoutubeApiService } from '../../services/youtube-api.service';


@Component({
  selector: 'oorga-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {
  
  apiKey:string='AIzaSyAkc_Bd_mpyn-MdyI91dp87AQqIRyZQVNM';
  ngOnInit(): void {
    this.ytApi.fetchTrendingVideos(this.apiKey,'IN').subscribe(data=>{
      this.thumbnailData=data['items'];
    });
  }

  constructor(private ytApi: YoutubeApiService){
   
  }
  
  public thumbnailData: any[];
  
  updateThumbnails(data){
    this.thumbnailData=data['items'];
  }
}



