import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'ViewCountFormatPipe'
})
export class ViewCountFormatPipe implements PipeTransform {

  transform(value: number): string {

    if (!value) return '';
    let million: number = 1000000;
    let thousand: number = 1000;
    let billion: number = 1000000000;
    let trillion: number = 1000000000000;
    let number: number = Math.round(value);
    if ((number >= thousand) && (number < million)) {
      let fraction: number = this.calculateFraction(number, thousand);
      return Math.round(fraction) + "K";
    } else if ((number >= million) && (number < billion)) {
      let fraction: number = this.calculateFraction(number, million);
      return this.truncateSignigifcantDigits(fraction, 1) + "M";
    } else if ((number >= billion) && (number < trillion)) {
      let fraction: number = this.calculateFraction(number, billion);
      return this.truncateSignigifcantDigits(fraction, 1) + "B";
    }
    return value.toString();
  }

  calculateFraction(number: number, divisor: number): number {
    var truncate = (number * 10 + (divisor / 2)) / divisor;
    var fraction = truncate * 0.10;
    return fraction;
  }


  truncateSignigifcantDigits(num: number, significantDigits: number): number {
    let y: number = Math.pow(10, significantDigits);
    return Math.trunc(num * y) / y;
  }

}



